(function($) {
  $(document).ready(function() {
    console.log("ready");

    $(".dd-accordion__header-button").on("click", function() {
      $(this)
        .closest("dd-accordion-group")
        .find(".dd-accordion")
        .removeClass("open");

      $(this)
        .closest(".dd-accordion")
        .toggleClass("open");
    });

    // Income calculator
    $(".income-calculator-calculate").on("click", function() {
      const parent = $(this).closest(".income-calculator");

      let selectedBuilding = parent
        .find(".income-calculator-buildings-selector")
        .val();
      let selectedRoomsQty = parent
        .find(".income-calculator-rooms-qty-selector")
        .val();

      console.log({
        selectedBuilding,
        selectedRoomsQty,
      });

      parent.find(".income-calculator__footer-placeholder").hide();
      parent
        .find(".income-calculator__footer-result")
        .show()
        .html("200 000 тг/мес");
    });

    var mySwiper = new Swiper(".property-card__gallery", {
      // loop: true,
      // If we need pagination
      pagination: {
        el: ".swiper-pagination",
        type: "custom",
        // renderBullet: function(index, className) {
        //   return '<span class="' + className + '">' + (index + 1) + "</span>";
        // },

        renderCustom: function(swiper, current, total) {
          const bulletsNumber = swiper.params.loop
            ? swiper.slides.length - 2
            : swiper.slides.length;
          let bulletsHtml = "";

          for (let index = 0; index < bulletsNumber; index++) {
            let isActive = index === 0 ? "swiper-pagination-bullet-active" : "";
            bulletsHtml += `<span class="swiper-pagination-bullet ${isActive}"></span>`;
          }

          return `
            <div class="pag-fraction">
            <span>${current}</span>
            /
            ${total}
            </div>
            <div class="pag-bullets swiper-pagination-bullets">${bulletsHtml}</div>
          `;
        },
      },
      on: {
        slideChange: function() {
          const isLoop = this.params.loop;
          const slidesNumber = this.slides.length;

          let prevIndex = this.previousIndex;
          let activeIndex = this.activeIndex;
          console.log(activeIndex);

          const $pag = $(this.pagination.$el);
          const bullets = $pag.find(".swiper-pagination-bullet");
          bullets.removeClass("swiper-pagination-bullet-active");

          if (isLoop) {
            $(bullets[activeIndex - 1]).addClass(
              "swiper-pagination-bullet-active",
            );

            if (activeIndex === 0) {
              $(bullets[bullets.length - 1]).addClass(
                "swiper-pagination-bullet-active",
              );
            }
            if (activeIndex === slidesNumber - 1) {
              $(bullets[0]).addClass("swiper-pagination-bullet-active");
            }
          } else {
            $(bullets[activeIndex]).addClass("swiper-pagination-bullet-active");
          }
        },
      },

      // Navigation arrows
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
    });

    var lightbox = new SimpleLightbox(".gallery-id-1", {
      // captionDelay: 400,
      loop: false,
    });
  });
})(jQuery);
